const FilterBar = () => {
  return (
     <section className="py-10 px-6 max-w-7xl mx-auto">
        <div className="flex flex-col md:flex-row gap-4 items-center justify-between">
          <input type="text" placeholder="Search Here" className="border px-4 py-2 rounded w-full md:w-1/3" />
          <select className="border px-4 py-2 rounded w-full md:w-1/4">
            <option>Choose Minority Type</option>
          </select>
          <select className="border px-4 py-2 rounded w-full md:w-1/4">
            <option>Choose Price Range</option>
          </select>
          <button className="bg-primary text-white px-6 py-2 rounded">Search Here</button>
        </div>
      </section>
  );
};

export default FilterBar;
